*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

body{
  /* background-color: #111; */
  background-image: url(./components/images/espacio.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  overflow: hidden !important;
  font-size: 18px;
}

.infoPlanetsContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
}

li b{
  color: #e11;
}

/* INICIO */

.boton{
  width: 140px;
  height: 40px;
  background-color: #f1f1f1;
  border-radius: 50px;
  border-color: #444;
  margin: 0 10px;
  font-weight: 700;
  letter-spacing: .5px;
}

.consejo{
  margin-top: .5em;
  color: #f1f1f1;
  text-align: center;
  font-weight: 500;
}

/* BOTONES INFORMATIVOS */

.abrirInfografia{
  width: 50px;
  height: 50px;
  position: absolute;
  right: .2em;
  top: .4em;
  display: flex;
  border: 1px solid #000;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  font-size: 35px;
  color: #000;
}

.informacionCerrada{
  font-size: 0;
  transition: .3s;
  width: 0;
  position: relative;
  float: right;
  margin-right: 4.5em;
  margin-top: 1em;
  height: 0;
}

.cerrarButtonActivo{
  right: .5rem;
  bottom: .5rem;
  font-size: 0;
  z-index: 0;
  transition: .3s;
  background-color: transparent;
  border: none;
  position: absolute;
}

.cerrarButton{
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: .2rem .4rem;
  border-radius: 5px;
  transition: .2s;
  position: absolute;
  right: .5rem;
  bottom: .5rem;
}

.cerrarButton:hover{
  color: #e11;
  background-color: #111;
}

.informacion{
  position: relative;
  float: right;
  margin-right: 4.5em;
  margin-top: 1em;
  width: 40vw;
  height: min-content;
  border-radius: 5px;
  border-top-right-radius: 0;
  padding: .9em;
  font-size: .9em;
  text-align: center;
  transition: .3s;
  background-color: #f1f1f1;
  z-index: 10;
}

/* REDES SOCIALES */

.boton:hover{
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.808);
}

/* SOL */

.Sol{
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 10em;
  height: 10em;
  background-image: url(./components/images/sol2.jpg);
  box-shadow: 0 0 50px 50px gold;
  animation: rotacionSolar 30s infinite linear;
  background-size: 200%;
  background-repeat: repeat;
}

@keyframes rotacionSolar{
  from{
      background-position: 0;
  }

  to{
      background-position: 200%;
  }
}

/* MERCURIO */

.Mercurio{
  width: 3em;
  height: 3em;
  background-image: url(./components/images/mercurio.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarMercurio 12s infinite linear;
  background-size: 200%;
}

.planetaIndividual{
  height: fit-content;
  width: fit-content;
}

@keyframes transladarMercurio{
  0%{
      left: 40vw;
      z-index: 2;
      top: 37.5vh;
  }

  48%{
      z-index: 2;
  }

  50%{
      z-index: -1;
      left: 60vw;
      top: 55vh;
  }

  99%{
      z-index: -1;
  }

  100%{
      left: 40vw;
      top: 37.5vh;
  }
}

/* VENUS */

.Venus{
  width: 4.5em;
  height: 4.5em;
  background-image: url(./components/images/venus.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarTierra 14s infinite linear;
  background-size: 200%;
}

@keyframes transladarVenus{
  0%{
      left: 35vw;
      z-index: 2;
      top: 30vh;
  }

  48%{
      z-index: 2;
  }

  50%{
      z-index: -2;
      left: 65vw;
      top: 60vh;
  }

  99%{
      z-index: -2;
  }

  100%{
      left: 35vw;
      top: 30vh;
  }
}

/* TIERRA */

.Tierra{
  width: 5em;
  height: 5em;
  background-image: url(./components/images/tierra.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarTierra 16s infinite linear;
  background-size: 200%;
  top: 30vh;
  color: #f1f1f1;
  display: flex;
}

@keyframes transladarTierra{
  0%{
      left: 30vw;
      z-index: 2;
      top: 30vh;
  }

  48%{
      z-index: 2;
  }

  50%{
      z-index: -3;
      left: 70vw;
      top: 60vh;
  }

  99%{
      z-index: -3;
  }

  100%{
      left: 30vw;
      top: 30vh;
  }
}

/* MARTE */

.Marte{
  width: 3.5em;
  height: 3.5em;
  background-image: url(./components/images/marte.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarMarte 18s infinite linear;
  background-size: 200%;
}

@keyframes transladarMarte{
  0%{
      left: 25vw;
      z-index: 3;
      top: 27.5vh;
  }

  48%{
      z-index: 3;
  }

  50%{
      z-index: -4;
      left: 75vw;
      top: 62.5vh;
  }

  99%{
      z-index: -4;
  }

  100%{
      left: 25vw;
      top: 27.5vh;
  }
}

/* JUPITER */

.Jupiter{
  width: 6em;
  height: 6em;
  background-image: url(./components/images/jupiter.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarJupiter 20s infinite linear;
  background-size: 200%;
}

@keyframes transladarJupiter{
  0%{
      left: 20vw;
      z-index: 4;
      top: 25vh;
  }

  48%{
      z-index: 4;
  }

  50%{
      z-index: -5;
      left: 80vw;
      top: 65vh;
  }

  99%{
      z-index: -5;
  }

  100%{
      left: 20vw;
      top: 25vh;
  }
}

/* SATURNO */

.Saturno{
  width: 5.5em;
  height: 5.5em;
  background-image: url(./components/images/saturno.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarSaturno 23s infinite linear;
  background-size: 150%;
}

@keyframes transladarSaturno{
  0%{
      left: 17vw;
      z-index: 5;
      top: 25vh;
  }

  48%{
      z-index: 5;
  }

  50%{
      z-index: -6;
      left: 83vw;
      top: 65vh;
  }

  99%{
      z-index: -6;
  }

  100%{
      left: 17vw;
      top: 25vh;
  }
}

/* URANO */

.Urano{
  width: 4.7em;
  height: 4.7em;
  background-image: url(./components/images/urano.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarUrano 26s infinite linear;
  background-size: 250%;
}

@keyframes transladarUrano{
  0%{
      left: 14vw;
      z-index: 6;
      top: 25vh;
  }

  48%{
      z-index: 6;
  }

  50%{
      z-index: -7;
      left: 86vw;
      top: 65vh;
  }

  99%{
      z-index: -7;
  }

  100%{
      left: 14vw;
      top: 25vh;
  }
}

/* NEPTUNO */

.Neptuno{
  width: 5em;
  height: 5em;
  background-image: url(./components/images/neptuno.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarNeptuno 29s infinite linear;
  background-size: 200%;
}

@keyframes transladarNeptuno{
  0%{
      left: 11vw;
      z-index: 7;
      top: 25vh;
  }

  48%{
      z-index: 7;
  }

  50%{
      z-index: -8;
      left: 89vw;
      top: 65vh;
  }

  99%{
      z-index: -8;
  }

  100%{
      left: 11vw;
      top: 25vh;
  }
}

/* PLUTON */

.Pluton{
  width: 3em;
  height: 3em;
  background-image: url(./components/images/pluton.jpg);
  animation: rotarPlaneta 6s infinite linear,
  transladarPluton 30s infinite linear;
  background-size: 300%;
}

@keyframes transladarPluton{
  0%{
      left: 8vw;
      z-index: 8;
      top: 25vh;
  }

  48%{
      z-index: 8;
  }

  50%{
      z-index: -9;
      left: 92vw;
      top: 65vh;
  }

  99%{
      z-index: -9;
  }

  100%{
      left: 8vw;
      top: 25vh;
  }
}

/* PLANETAS */

.infoGeneralPlaneta{
  width: 60vw;
  height: min-content;
  border-radius: 10px;
  z-index: 10;
  background-color: rgba(255, 254, 254, 0.753);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  padding: .9em;
  font-size: .9em;
  display: block;
  transition: .3s;
}

.closeInfoPanetas{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  font-size: 0;
  z-index: 0;
  transition: .3s;
  background-color: transparent;
  border: none;
  position: absolute;
  width: 0;
  height: 0;
  padding: 0;
}

.infoGeneralPlaneta ul{
  list-style-type: none;
}

.planeta{
  display: flex;
  justify-content: flex-end;
  position: absolute;
  border-radius: 50%;
  cursor: pointer;
}

.planeta:hover{
  animation-play-state: paused;
}

.nombreDelPlaneta{
  padding: 2px 5px;
  color: #f1f1f1;
  margin: -1rem -2rem 0 0;
  font-size: .9em;
}

@keyframes rotarPlaneta{
  from{
      background-position: 0;
  }

  to{
      background-position: 200%;
  }
}

@media only screen and (max-width: 1024px){

  .infoPlanetsContainer{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: .9em;
  }
}


@media only screen and (max-width: 600px){

  /* GENERAL */

  *{
      overflow: hidden;
      font-size: 11px;
  }

  /* INICIO */

  .boton{
      margin-top: 1rem;
      width: 40px;
      height: 40px;
      font-size: 2em;
  }

  /* CONSEJO */

  .consejo{
    display: none;
  }

  /* INFOGRAFIA */

  .informacion{
      width: 15em;
      height: 15em;
      padding: 1em;
      padding-bottom: 10em;
      font-size: 1.2em;
  }

  /* PLANETAS */

  .nombreDelPlaneta{
    color: transparent;
  }

  .infoGeneralPlaneta{
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 100;
      border-radius: 0;
      font-size: 1.2em;
      left: -1vw;
  }

  .infoGeneralPlaneta ul li{
      padding: 5em 2em 0;
  }

  .infoPlanetsContainer{
    align-items: flex-start;
  }
}
